.mainplayer{
    width:100%;
    height:87vh;
    background-color: #121212;
  
    grid-gap:10px;
    overflow-y:scroll;
    overflow-x: hidden;
    direction: ltr;
    /* scrollbar-width: none; */
}
.mainplayer::-webkit-scrollbar,.arrange::-webkit-scrollbar,.arrange1::-webkit-scrollbar {
    display: none;
  }
.banner{
    background-image:url('concert-.jpg');
    /* background-position-x:25px; */
    background-position-y:-265px;
    background-repeat: no-repeat;
    background-size: cover;
    margin:0 5px;
    opacity:0.8;
    height:45vh;
}
.arrange{
    display:grid;
    grid-template-columns:repeat(10,35vh);
    grid-gap:20px;
    background-color: black;
    margin:10px 5vh 10px 5vh;
    overflow-x:scroll;
    height:32vh;
   
    /* overflow-x: scroll; */
}
.box{
    /* margin:10px 8px 20px 8px; */
    width:30vh;
    height:28vh; 
}

.box:hover{
  opacity:1.2;
    
}
.box img{
    width:30vh;
    height:26vh;
    margin-left:2.5%;
    opacity:1.5; 
}
.box a {
    text-decoration: none;
    color:black;
}
.title{width:5fr;
color:white;
background-color: black;
height:8vh;
margin: 20px 10px 10px 7px;
text-align:center;
font-size:4rem;
}

.box h4{
    text-align: center;
    font-size: larger;
}
