.loader{
    position:absolute;
    top:40%;
    right:40vw;
    
}

.playlistspopup{
    height:60vh;
    width:75%;
    background-color: #000000;
    background-image: linear-gradient(315deg, #000000 0%, #414141 74%);
    position:absolute;
    top:100px;
    left:200px;
    color:white;   
}
.playlistspopup h1{
    cursor: pointer;}
